<template>
    <el-row >
      <el-col :span="24">
        <el-menu class="menu"   router  background-color="#2F323A" text-color="#fff" active-text-color="#ffd04b">
          <el-submenu index="1">
            <template slot="title">
              <i class="el-icon-setting"></i>
              <span>爬虫管理</span>
            </template>
            <el-menu-item-group>
              <el-menu-item index="manager" class="item">服务器总览</el-menu-item>
              <el-menu-item index="hello" class="item">自定义查询</el-menu-item>
            </el-menu-item-group>
          </el-submenu>
          <el-submenu index="2">
            <template slot="title">
              <i class="el-icon-sort"></i>
              <span>数据分析</span>
            </template>
            <el-menu-item-group>
              <el-menu-item index="hello" class="item">数据分析1</el-menu-item>
            </el-menu-item-group>
          </el-submenu>
        </el-menu>
      </el-col>
    </el-row>
</template>

<script>
export default {
  name: "Sidebar",
  computed: {
    onRoutes() {
      return this.$route.path;
    }
  },
  methods: {

  }
};
</script>

<style rel="stylesheet/scss" lang="scss">
.menu {
  border: 0px;
  text-align: left;
  .item{
    padding-left: 52px !important;
  }
}

</style>



