import axios from 'axios';
import { Message } from 'element-ui';

export function getSpider() {

  return new Promise((resolve, reject) => {
    axios.get(process.env.BASE_API+'/api/getSpider')
      .then(res => {
        res = res.data
        resolve(res);
      })
      .catch(err => {
        reject(err);
      })
  })

}


