import axios from 'axios';
import { Message } from 'element-ui';

export function loginByEmail(username, password) {
  const data = {
    username,
    password
  };
  return new Promise((resolve, reject) => {
    axios.post(process.env.BASE_API+'/api/login', data)
      .then(res => {
        res = res.data
        resolve(res);
      })
      .catch(err => {
        reject(error);
      })
  })

}






