// import Cookies from 'js-cookie';
import { loginByEmail } from '@/api/login';

function logout() {
  return new Promise(resolve => {
    setTimeout(() => {
      resolve([200, { "retCode": "10000", "msg": "成功", "time": "2017-06-27 15:19:03", "data": null }]);
    }, 100);
  })
}

const user = {
  state: {
    email: '',
    uid: sessionStorage.getItem('uid'),
    auth_type: '',
    status: '',
    token: sessionStorage.getItem('token'),
    // token: Cookies.get('X-Ivanka-Token'),
    roles: []
  },

  mutations: {
    SET_EMAIL: (state, email) => {
      state.email = email;
    },
    SET_UID: (state, uid) => {
      state.uid = uid;
    },
    SET_AUTH_TYPE: (state, type) => {
      state.auth_type = type;
    },
    SET_STATUS: (state, status) => {
      state.status = status;
    },
    SET_TOKEN: (state, token) => {
      state.token = token;
    },
    SET_ROLES: (state, roles) => {
      state.roles = roles;
    },
  },

  actions: {
    // 邮箱登录
    LoginByEmail({ commit }, userInfo) {
      const email = userInfo.email.trim();
      return new Promise((resolve, reject) => {
        // if(email == "cldev2018" && userInfo.password == "cldev2018"){
        //   const data =userMap["admin"].data;
        //   console.log("x")
        //   localStorage.token = data.tokenModel.adminId+'_'+data.tokenModel.token;
        //   localStorage.uid = data.adminInfo.adminId;
        //   commit('SET_TOKEN', data.tokenModel.adminId+'_'+data.tokenModel.token);
        //   commit('SET_EMAIL', data.adminInfo.email);
        //   commit('SET_ROLES', ['admin']);
        //   commit('SET_UID', data.adminInfo.adminId);
        //   resolve();
        // }else{
        //   reject("账号密码不正确")
        // }
        loginByEmail(email, userInfo.password).then(response => {
          console.log(response)
          if (response.success === true) {
            const data = response.data;
            sessionStorage.token = data.token;
            sessionStorage.uid = data.uid;
            commit('SET_TOKEN', data.token);
            commit('SET_EMAIL', email);
            commit('SET_ROLES', ['admin']);
            commit('SET_UID', data.uid);
            resolve();
          } else {
            reject(response.msg);
          }
        }).catch(error => {
          reject(error);
        });
      });
    },

    // 登出
    LogOut({ commit }) {
      return new Promise((resolve, reject) => {
        setTimeout(() => {
          commit('SET_TOKEN', '');
          commit('SET_ROLES', []);
          commit('SET_EMAIL',"");
          commit('SET_UID', "");
          sessionStorage.removeItem("token");
          sessionStorage.removeItem("uid");
          resolve();
        }, 100);
      });
    }

  }
};

export default user;



