<template>
<div>
    <el-collapse accordion="" @change="handleChange">
      <el-collapse-item v-for="spider of spiders" :title="spider.spiderName | capitalize"  :name="spider.spiderName" :key="spider.spiderName"   >
          <el-table :data="spider.servers" border style="width: 100%"  :span-method="objectSpanMethod" show-summary>
                                    <el-table-column fixed="left" label="状态"  width="60" class-name="colum-center">
                <template slot-scope="scope"> 
                   <i :class="spider.servers[scope.$index].state? 'el-icon-success':'el-icon-warning'"></i>
                  <!-- <el-switch v-model="spider.servers[scope.$index].state"  active-color="#13ce66" inactive-color="#ff4949" disabled></el-switch> -->
                </template>
                        <!-- <el-tag
          :type="scope.row.tag === '家' ? 'primary' : 'success'"
          close-transition>{{scope.row.tag}}</el-tag> -->
            </el-table-column>
            <el-table-column fixed prop="hostIp" label="服务器ip" width="150"></el-table-column>
            <el-table-column fixed prop="hostName" label="服务器名字" ></el-table-column>

            <el-table-column fixed prop="table" label="表" ></el-table-column>
            <el-table-column fixed prop="fourHoursCount" label="4小时内爬取条数" ></el-table-column>
            <el-table-column fixed prop="oneDayCount" label="1天内爬取条数" ></el-table-column>
                      <el-table-column fixed prop="twoDayCount" label="2天内爬取条数" ></el-table-column>
            <el-table-column fixed prop="countRecord" label="爬取总条数" ></el-table-column>

          </el-table>
      </el-collapse-item>
    </el-collapse>
</div>
</template>
<script>
import { getSpider } from "@/api/getSpider";
export default {
  mounted: function() {
    console.log(this.spiders);
    let self = this;
    getSpider().then(res => {
      if (res) {
        res = JSON.parse(res.data)
        console.log(res);
        //格式转换
        let newRes = [];
        res.forEach(_res => {
          let item = [];
          _res.servers.forEach(_server => {
            _server.tables.forEach(_table => {
              let _obj = {};
              _obj.hostIp = _server.hostIp;
              _obj.hostName = _server.hostName;
              _obj.state = _obj.state;
              _obj.countRecord = _table.countRecord;
              _obj.fourHoursCount = _table.fourHoursCount;
              _obj.countRecord = _table.countRecord;
              _obj.fourHoursCount = _table.fourHoursCount;
              _obj.oneDayCount = _table.oneDayCount;
              _obj.table = _table.table;
              _obj.twoDayCount = _table.twoDayCount;
              _obj.tableLength = _server.tables.length;
              item.push(_obj);
            });
          });
          newRes.push({ spiderName: _res.spiderName, servers: item });
        });
        console.log(newRes);
        self.spiders = newRes;
      }
    });
  },
  name: "manager",
  methods: {
    handleChange(spild) {
      console.log(spild);
    },
    objectSpanMethod({ row, column, rowIndex, columnIndex }) {
      if (columnIndex === 0 || columnIndex === 1 || columnIndex === 2) {
        if (rowIndex % row.tableLength === 0) {
          return {
            rowspan: row.tableLength,
            colspan: 1
          };
        } else {
          return {
            rowspan: 0,
            colspan: 0
          };
        }
      }
    }
  },
  filters: {
    capitalize: function(value) {
      if (!value) return "";
      value = value.toString();
      return value.charAt(0).toUpperCase() + value.slice(1);
    }
  },
  data() {
    return {
      spiders: []
    };
  }
};
</script>
<style rel="stylesheet/scss" lang="scss">
.el-collapse-item__header {
  padding-left: 18px;
  font-size: 18px;
}
.el-collapse-item__content {
  padding-bottom: 0px;
}
.is-active .el-collapse-item__header {
  color: #178aeb;
}
.el-icon-warning {
  color: red;
  font-size: 22px;
  text-align: center;
}
.el-icon-success {
  color: green;
  font-size: 22px;
  text-align: center;
}
.colum-center {
  text-align: center;
}

</style>




